\n\n.header {\n  gap: 40px;\n  display: flex;\n  justify-content: center;\n  flex-wrap: wrap;\n\n  @media (width <= $mobile-breakpoint) {\n    display: grid;\n    gap: 15px;\n    grid-template-columns: repeat(2, 1fr);\n\n    > :nth-child(2) {\n      order: 1;\n      grid-column: span 2;\n    }\n  }\n}\n\n.infoText {\n  font-weight: 400;\n  font-size: clamp(20px, 3vw, 36px);\n  text-shadow: 0px 0px 15px var(--blue-light);\n\n  * {\n    font-weight: inherit;\n  }\n}\n\n.info {\n  margin-top: 40px;\n  display: flex;\n  justify-content: center;\n  position: relative;\n\n  &::before {\n    content: '';\n    position: absolute;\n\n    margin-top: auto;\n    height: 50%;\n    z-index: -1;\n    filter: blur(20px);\n\n    background: linear-gradient(\n      90deg,\n      rgba(0, 0, 0, 0) 3.92%,\n      rgba(31, 203, 255, 0.2) 40.95%,\n      rgba(31, 203, 255, 0.2) 68.97%,\n      rgba(0, 0, 0, 0) 100%\n    );\n\n    @keyframes slide {\n      from {\n        left: 0;\n        right: 0;\n      }\n\n      to {\n        left: 150px;\n        right: 150px;\n      }\n    }\n\n    animation: slide 6s infinite alternate;\n  }\n\n  display: grid;\n  grid-template-columns: 1fr auto 1fr;\n  align-items: center;\n  gap: 0 3vw;\n\n  position: relative;\n\n  .title {\n    text-align: right;\n\n    .keyword {\n      color: var(--blue-light);\n    }\n\n    .lastTextBlock {\n      width: 100%;\n      display: inline-block;\n    }\n\n    @media ($mobile-breakpoint <= width <= 680px) {\n      .keyword {\n        display: inline-block;\n        // to wrap to new line\n        width: 50%;\n      }\n\n      .lastTextBlock {\n        display: inline;\n      }\n    }\n  }\n\n  .graphWrapper {\n    position: relative;\n\n    width: var(--graph-size);\n    height: var(--graph-size);\n    background-color: black;\n\n    overflow: hidden;\n    box-shadow: 0px 0px 5px 1px rgba(31, 203, 255, 0.3),\n      0px 0px 42px 0px rgba(31, 203, 255, 0.42);\n\n    &,\n    &::after {\n      border-radius: 50%;\n    }\n\n    &::after {\n      content: '';\n      position: absolute;\n      pointer-events: none;\n      left: 0;\n      right: 0;\n      top: 0;\n      bottom: 0;\n      box-shadow: inset 0 0 50px 20px black;\n    }\n  }\n\n  @media (width <= $mobile-breakpoint) {\n    display: unset;\n\n    .title {\n      &,\n      > * {\n        font-size: 26px !important;\n      }\n\n      .lastTextBlock {\n        width: 100%;\n        display: inline-block;\n      }\n\n      min-width: unset;\n    }\n\n    .graphWrapper {\n      position: absolute;\n\n      left: calc(-50px + 100vw - (330px * 0.44));\n      top: 60px;\n    }\n  }\n}\n\n.advantages {\n  display: grid;\n  margin: -40px auto 0;\n  gap: 0px 5vw;\n\n  grid-template-columns: repeat(3, minmax(0, 33%));\n  grid-template-rows: repeat(2, 1fr);\n  text-align: center;\n  justify-items: center;\n\n  li {\n    width: 188px;\n    min-height: 70px;\n\n    animation: appear 3s;\n\n    @keyframes appear {\n      from {\n        opacity: 0;\n      }\n\n      to {\n        opacity: 1;\n      }\n    }\n  }\n\n  li:nth-child(1) {\n    grid-column: 1 / 2;\n    grid-row: 1;\n  }\n\n  li:nth-child(2) {\n    margin-top: -30px;\n    grid-column: 2 /3;\n    grid-row: 2;\n  }\n\n  li:nth-child(3) {\n    grid-column: 3 / 4;\n    grid-row: 1;\n  }\n\n  @media (width <= $mobile-breakpoint) {\n    margin: 20px 0 0 !important;\n\n    display: flex;\n    flex-direction: column;\n    gap: 20px 0;\n\n    li:nth-child(2) {\n      margin-top: unset;\n    }\n  }\n}\n"],"sourceRoot":""}]);
